<template>
  <Row :gutter="18" type="flex" justify="center" align="top">
    <Col span="5">
      <my-tag-selector @change="change" :tags="tags"></my-tag-selector>
    </Col>
    <Col span="14">
      <div v-if="dataIsEmpty">
        <Col :xs="24" :sm="12" :lg="8"  class="card"  v-for="(data,index) in currentDatas" :key="index">
          <Card>
            <div slot="title">
              <h3 style="display: inline-block;">{{data.title}}</h3>
            </div>
            <a slot="extra" v-bind:href="finalPath(data.title)" target="_blank">
              <Icon type="md-game-controller-b" />开始游戏
            </a>
            <div class="card-body">
              <img v-lazy="finalImg(data.title)">
            </div>
          </Card>
        </Col>
      </div>
      <div class="card ivu-col-span-9" v-else>
        <Card>
          <div slot="title">
            当前模块无数据
          </div>
          <div class="card-body">
            <img v-lazy="defaultImgUrl">
          </div>
        </Card>
      </div>
    </Col>
    <Col span="5"></Col>
  </Row>
</template>

<script>
import Data from '../../../static/mork/works.json'
// import MyTagSelector from '../widget/MyTagSelector'

export default {
  name: 'MyWork',
  components: {
    MyTagSelector: resolve => {
      require(['../widget/MyTagSelector'], resolve)
    }
  },
  data () {
    return {
      datas: [],
      tags: [],
      currentDatas: [],
      currentTag: '',
      defaultImgUrl: '@/assets/image/loading.gif'
    }
  },
  created () {
    this.datas = Data
    for (let i = 0; i < this.datas.length; i++) {
      if (this.datas[i].hasOwnProperty('tag')) {
        this.tags.push(this.datas[i].tag)
      }
    }
    this.currentTag = '全部'
    this.currentDatas = this.datas.filter(x => x.hasOwnProperty('tag') || this.currentTag === '全部')
  },
  computed: {
    dataIsEmpty: function () {
      return this.datas.length !== 0
    }
  },
  methods: {
    finalPath: function (title) {
      return `../../static/mork/works/${title}/`
    },
    finalImg: function (title) {
      return `../../static/mork/works/${title}/logo.png`
    },
    change (value) {
      this.currentTag = value
      this.currentDatas = this.datas.filter(x => x.hasOwnProperty('tag') || this.currentTag === '全部')
    }
  }
}
</script>

<style scoped>
  .card-body{
    width: 100%;
    height: 0;
    margin-bottom: 100%;
  }
  .card-body img{
    width:100%;
    height: 100vm;
  }
  .card{
    padding:10px;
  }
</style>



